import React, { Component } from 'react';
import './App.css';
import config from './constants/config';
var md5 = require('md5');
var base64 = require('base-64');

class App extends Component {
  constructor(props) {

    super(props);
    this.date = Date.now();
    this.params = JSON.parse(base64.decode(this.props.location.search.substring(1)));
    this.signature = md5(config.apiKey + "~" + config.merchantId + "~" + this.params.comboType + this.date + "~" + this.params.price + "~COP");
    this.state = {}
  }

  render() {
    return (

      <div className="container">

        <div className="section center orange darken-2 container">

          <img className="responsive-img container" alt="meniu-img" src="https://meniu.com.co/wp-content/uploads/2018/08/cropped-meniu-blanco.png"></img>

        </div>

        <div className="center">
          <br></br>
          <br></br>
          <h5><b>Tu compra es:</b></h5>
          <h6>{this.params.comboType}</h6>
          <br></br>
          <h5><b>Por un valor total de:</b></h5>
          <h6>{'$' + parseFloat(this.params.price).toFixed(2)}</h6>
          <br></br>
          <br></br>
          <form method="post" action="https://checkout.payulatam.com/ppp-web-gateway-payu/">
            <input name="merchantId" type="hidden" value={config.merchantId} />
            <input name="accountId" type="hidden" value={config.accountId} />
            <input name="description" type="hidden" value={this.params.comboType + " Combo Meniu"} />
            <input name="referenceCode" type="hidden" value={this.params.comboType + this.date} />
            <input name="amount" type="hidden" value={parseFloat(this.params.price).toFixed(2)} />
            <input name="tax" type="hidden" value={(parseFloat(this.params.price) * (1 - 1 / 1.19)).toFixed(2)} />
            <input name="taxReturnBase" type="hidden" value={(parseFloat(this.params.price) / 1.19).toFixed(2)} />
            <input name="currency" type="hidden" value="COP" />
            <input name="signature" type="hidden" value={this.signature} />
            <input name="test" type="hidden" value="0" />
            <input name="buyerEmail" type="hidden" value={this.params.userEmail} />
            <input name="buyerFullName" type="hidden" value={this.params.userFullName} />
            <input name="responseUrl" type="hidden" value={config.responseUrl} />
            <input name="confirmationUrl" type="hidden" value={config.confirmationUrl} />
            <input name="extra1" type="hidden" value={this.params.comboType} />
            <input name="extra2" type="hidden" value={this.params.userEmail} />
            <input name="extra3" type="hidden" value={this.params.paymentId} />
            <input className="waves-effect waves-light btn" name="Submit" type="submit" value="Confirmar Compra" />
          </form>
        </div>

      </div>
    )
  }
}

export default App;
